.home {
    display: grid;
    min-height: 100%;
    animation: 5s infinite alternate background;
    -moz-animation: 5s infinite alternate background;
    background-size: cover;
    justify-content: center;
    grid-template-columns: auto;
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas: 
    "."
    "logo"
    ".";
}

@keyframes background {
    0% {
      background-image: url('../assets/img/S1.png');
    }
    25% {
      background-image: url('../assets/img/S1.png');
    }
    75% {
      background-image: url('../assets/img/S2.png');
    }
    100% {
      background-image: url('../assets/img/S2.png');
    }
}

.home h1 {
    margin: 0 0 10 0;
    color: white;
    font-size: 26px;
    height: auto;
}

.home img {
    width: 25%;
    height: auto;
}

.logo-header {
    grid-area: logo;
    align-self: center;
    padding: 10px;
}

@media (max-width: 767px) {
    .home {
        display: grid;
        width: 100%;
        animation: 5s infinite alternate background;
        background-size: cover;
        justify-content: center;
        grid-template-columns: auto;
        grid-template-rows: repeat(3, 1fr);
        grid-template-areas: 
        "."
        "logo"
        ".";
    }

    @keyframes background {
        0% {
          background-image: url('../assets/img/S1.png');
        }
        25% {
          background-image: url('../assets/img/S1.png');
        }
        75% {
          background-image: url('../assets/img/S2.png');
        }
        100% {
          background-image: url('../assets/img/S2.png');
        }
    }

    .home h1 {
        color: white;
        font-size: 26px;
    }

    .home img {
        width: 25%;
        height: auto;
    }

    .logo-header {
        grid-area: logo;
        align-self: center;
    }
}
